@import "#main-styles/variables";
@import "#main-styles/colorVariables";

.wrapper {
  position: relative;
  overflow: hidden;
  position: relative;
  order: 2;
  min-height: 28.5rem;
  z-index: 2;
}

.buttonsBlock {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.noWinner {
  position: fixed;
  width: 90%;
}

.containerQueue {
  position: relative;
  width: 100%;
  height: 5rem;
}

.queue {
  top: 50%;
  position: relative;
}

.lock {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  animation: move 2s linear infinite alternate-reverse;
  fill: $zerroFigure;
}

@keyframes move {
  0% {
    bottom: 18%;
  }
  100% {
    bottom: 23%;
  }
}

@media (min-width: $L) {
  .wrapper {
    order: 1;
  }
  .wrapper {
    position: absolute;
    bottom: 0;
    left: 0;
  }
}
