@import '#main-styles/transition-variables';
@import '#main-styles/colorVariables';
@import '#main-styles/variables';
/* @import '' */
.wrapper{
  position: fixed;
  top: -70%;
  left: 0;
  width: 100%;
  height: 70%;
  background-color: $menuCollor;
  transition: .5s;
  transition-delay: $menuDelay;
  border: 5px solid $lineMenu_1;
  overflow: hidden;
  z-index: 3;
}


.activeWrap{
  top: 0;
}

.connectBlock{
  position: relative;
  transform: translateX(-2rem);
  display: flex;
  flex-direction: column;
  gap: .5rem
}
