.choise{
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: .5rem;
}


.burgerPosition{
  position: absolute;
  right: 1rem;
  bottom: 1rem;
}


@media(min-width: $L){
  .wrapper{
    position: absolute;
    top: 4rem;
    left: auto;
    right: 0;
    width: 20rem;
    height: 75%;
    background-color: transparent;
    transition: 0s;
    transition-delay: 0;
    border: none; // development
    z-index: 3;
    border: 2px solid $gridCollor;
    border-radius: .5rem;
  }

.burgerPosition{
  display: none;
}


}