@import "#main-styles/variables.scss";
@import "#main-styles/colorVariables";
.wrapper {
  min-width: 14rem;
  font-size: 1.5rem;
  // background-color: rgba(36, 51, 166, 0.294);
  background-color: $interactiveModalMessage;
  height: 3rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  border: 2px solid rgb(0, 12, 70);
  color: black;
}
.activeWrap {
  background-color: rgba(10, 107, 57, 0.765);
  border: 2px solid rgb(0, 55, 19);
  box-shadow:
    inset 0 0 4px 3px white,
    0 0 4px 5px white;
}

@media (min-width: $L) {
  .wrapper {
    height: 2.5rem;
    font-size: 1.4rem;
    &:hover {
      background-color: rgba(248, 248, 248, 0.765);
      color: black;
    }
  }
}
